import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Progress from 'nprogress';


class Header extends Component {

	onItemClick (event) {
		Progress.start();
	}

  render(){
  	return (
	    <div>

	    <nav className="navbar navbar-expand-lg navbar-light bg-white">
	      <Link onClick={this.onItemClick} className="navbar-brand" to={{pathname: "/", state: "desiredState"}}><img src={require("../styles/4-assets/0-logo.png")} id="logo" /></Link>
	      <button className="navbar-toggler hamburgButton" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
	        <span className="navbar-toggler-icon hamburg"></span>
	      </button>

	      <div className="collapse navbar-collapse" id="navbarsExampleDefault">
	        <ul className="navbar-nav ml-auto">
	       	  <li className="nav-item">
	            <Link onClick={this.onItemClick} className="nav-link" to={{pathname: "/students", state: "desiredState"}} >For Students</Link>
	          </li>
	          <li className="nav-item">
	            <Link onClick={this.onItemClick} className="nav-link" to={{pathname: "/professionals", state: "desiredState"}} >For Professionals</Link>
	          </li>
	          <li className="nav-item">
	            <Link onClick={this.onItemClick} className="nav-link" to={{pathname: "/about", state: "desiredState"}} >About Us</Link>
	          </li>
	          <li className="nav-item">
	            <a className="btn-outline-red btn btn-sign-up" href="https://survey.az1.qualtrics.com/jfe/form/SV_b8X2lMbbXGTaAcJ" role="button">Sign Up</a>
	          </li>
	        </ul>
	      </div>
	    </nav>

	    </div>
	  );
  }
}

export default Header;



