import React, { Component } from 'react'
import { Switch, Route, browserHistory, withRouter } from 'react-router-dom'
import Home from './Home'
import ForStudents from './ForStudents'
import ForProfessionals from './ForProfessionals'
import About from './About'
import NotFoundPage from './NotFoundPage'
import Progress from 'nprogress'


class App extends Component {

	/*componentWillMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      console.log("on route change");
      Progress.start();
    });
  }
  componentWillUnmount() {
    this.unlisten();
  }*/

  render(){
  	return(
  		<main>
		    <Switch>
		      <Route exact path='/' component={Home}/>
          <Route exact path='/students' component={ForStudents}/>
          <Route exact path='/professionals' component={ForProfessionals}/>
		      <Route exact path='/about' component={About}/>
		      <Route component={NotFoundPage} />
		    </Switch>
		  </main>
  		);
  }
}

export default withRouter(App)



